<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <a download  class="f-small main-c mr5" href="../gy-files/template/校办绩效考核登记表.xlsx" >下载模板</a>
        <label for="importDom">
          <div class="btn-main btn-upload">导入绩效考核</div>
        </label>
        <input accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
               @change="upload($event)" ref="upload" id="importDom" type="file" hidden>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="12%">教研组</th>
          <th width="7%">姓名</th>
          <th width="7%">出勤</th>
          <th width="7%">业绩</th>
          <th width="7%">德育</th>
          <th width="7%">
            <p>荣誉加分</p>
            <p class="f-small">(党务)</p>
          </th>
          <th width="7%">
            <p>荣誉加分</p>
            <p class="f-small">(工会)</p>
          </th>
          <th width="7%">
            <p>荣誉加分</p>
            <p class="f-small">(教研)</p>
          </th>
          <th width="7%">加分项</th>
          <th width="7%">否决项</th>
          <th width="7%">总分</th>
          <th width="18%">备注</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in table.list">
          <td>{{item.teachGroupName}}</td>
          <td>{{item.teacherName}}</td>
          <td>{{item.roster}}</td>
          <td>{{item.performance}}</td>
          <td>{{item.education}}</td>
          <td>{{item.honorPartyWork}}</td>
          <td>{{item.honorSociety}}</td>
          <td>{{item.honorResearch}}</td>
          <td>{{item.addTotal}}</td>
          <td>{{item.vetoItem}}</td>
          <td>{{item.total}}</td>
          <td>{{item.remark}}</td>
        </tr>
        </tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>

  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "教师考核", path: ""}],
        searchData: [
          {type: 'select',title: '学期', width: '200px', notRemove: true, model: 'schoolTermId', option: []},
        ],
        table: {
          url: '/assessTeacherPerformance/queryAssessTeacherPerformancePage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            schoolTermId: null,
          }
        },
        // 学期列表
        termList: []
      }
    },
    methods: {
      // 导入
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/import/importAssessTeacherPerformance.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        this.$refs.upload.value = null;
        let res = await this.$ajax(obj)
        this.$remind('导入成功')
        this.$search(this.table)
      },
      // 获取学期
      async getTermList(){
        let res = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        res.forEach(e=>{
          this.searchData[0].option.push({
            value: e.schoolTermId, label: e.termName
          })
        })
        res.forEach(e=>{
          if(e.isDefault === 'YES'){
            this.table.query.schoolTermId = e.schoolTermId
          }
        })
      },
    },
    async mounted() {
      await this.getTermList()
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .btn-block
    .main-c
      vertical-align: top
      display: inline-block
      line-height: 30px
      margin-top: 2px
</style>
